@keyframes moveRight {
  0% {
    left: 45%;
    opacity: 0;
  }
  50% {
    left: 55%;
    opacity: 1;
  }
  100% {
    left: 50%;
  }
}
@keyframes moveLeft {
  0% {
    right: 55%;
    opacity: 0;
  }
  50% {
    left: 45%;
    opacity: 1;
  }
  100% {
    left: 50%;
  }
}
@keyframes appear {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
*,
*::before,
*::after {
  scroll-behavior: smooth;
  margin: 0;
  padding: 0;
  box-sizing: inherit;
  text-decoration: none;
}

@media only screen and (max-width: 78.125em) {
  html {
    font-size: 93.75%;
  }
}
@media only screen and (max-width: 56.25em) {
  html {
    font-size: 87.5%;
  }
}
@media only screen and (max-width: 37.5em) {
  html {
    font-size: 71.25%;
  }
}

body {
  box-sizing: border-box;
  font-family: "Lato", sans-serif !important;
}

.container {
  max-width: 71.25em;
  margin: 0 auto;
}
@media only screen and (max-width: 78.125em) {
  .container {
    max-width: 58.75em;
  }
}
@media only screen and (max-width: 56.25em) {
  .container {
    max-width: 46.25em;
  }
}
@media only screen and (max-width: 37.5em) {
  .container {
    max-width: 33.75em;
  }
}

.text-colored {
  color: #0077fe;
}

.mt-3 {
  margin-top: 1.25em;
}

.u-margin-bottom-tiny {
  margin-bottom: 0.4375em !important;
}

.u-margin-bottom-medium {
  margin-bottom: 2.5em !important;
}

.u-margin-bottom-big {
  margin-bottom: 3.75em !important;
}

.u-margin-bottom-huge {
  margin-bottom: 6.25em !important;
}

section {
  margin: 6.25em 0;
}

.u-text-center {
  text-align: center;
}

.center {
  display: flex;
  justify-content: center;
}

.content-wrap {
  padding: 0 1.875em;
}

.title {
  position: absolute;
  transform: translate(-50%, -50%);
  top: 50%;
  left: 50%;
}

li {
  list-style: none;
  white-space: nowrap;
}

ul {
  margin: 0 !important;
}

h1 {
  text-align: center;
  font-size: 3.75em !important;
  font-weight: 600 !important;
  margin: 0 !important;
  white-space: nowrap;
}

h2 {
  font-size: 2.1875em !important;
}

h3 {
  font-size: 1.25em !important;
}

h2,
h3 {
  text-transform: uppercase;
  font-weight: 600 !important;
  margin: 0 !important;
}

a {
  text-decoration: none !important;
}

a:focus {
  outline: transparent !important;
}

p {
  margin: 0 !important;
  color: #333 !important;
}

.btn, .btn:link, .btn:visited {
  text-decoration: none;
  font-weight: 600;
  padding: 0.9375em 1.875em;
  border-radius: 0.5em !important;
  border: none;
  text-transform: uppercase;
  position: relative;
  display: inline-flex;
  align-items: center;
  cursor: pointer;
  transition: all 0.3s;
  will-change: transform;
  box-shadow: 0.125em 0.375em 0.625em rgba(51, 51, 51, 0.15);
}
.btn:hover {
  transform: translateY(-2px);
  box-shadow: 0.125em 0.75em 0.625em rgba(51, 51, 51, 0.15);
}
.btn--color {
  background-color: #333 !important;
  color: #fff !important;
}
.btn:active {
  transform: scale(0.97);
}
.btn--submit:focus {
  outline-color: #0077fe !important;
}

.dropdown {
  position: relative;
  display: inline-block;
  transition: all 3s;
}
.dropdown:hover .dropdown__content {
  display: block;
}
.dropdown__content {
  display: none;
  position: absolute;
  background-color: #fff;
  padding-top: 1em;
  z-index: 1;
  transition: all 3s;
}

.input {
  outline: none;
  padding: 0.75em !important;
  border: 2px solid #d4d4d4 !important;
  border-radius: 5px;
  font-size: 1em;
  width: 100%;
}
.input:not(:last-child) {
  margin-bottom: 1.25em;
}
.input::placeholder {
  font-size: 1em;
}
.input:focus {
  outline-color: #0077fe !important;
}

.navigation {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 1.875em;
}
.navigation__logo {
  height: 6.25em;
}

.site-header {
  padding-top: 0 !important;
  margin: 0 !important;
  border-bottom-color: transparent !important;
}

.dropdown__content ul li a {
  font-size: 0.9375em;
  font-weight: 600;
  display: block;
  transition: all 0.3s;
  color: #565656 !important;
  padding: 0.625em 1.375em;
}
.dropdown__content ul li a:hover {
  background-color: #565656;
  color: #fff !important;
}

.menu {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.menu-item a {
  font-size: 0.9375em;
  text-transform: uppercase;
  transition: 0.3s;
  font-weight: 600;
  color: #333;
}
.menu-item a:hover {
  color: #0077fe;
}

.menu-header-menu-container .menu .menu-item:not(:last-child) {
  padding-right: 1.0625em;
}

.hidden-cross {
  display: none;
  font-size: 3rem;
  color: #333;
  cursor: pointer;
}
@media only screen and (max-width: 56.25em) {
  .hidden-cross {
    display: block;
  }
}

.sidebar {
  backdrop-filter: blur(1rem);
  position: fixed;
  display: none;
  top: 0;
  right: 0;
  height: 100vh;
  z-index: 11;
  width: 50%;
  background-color: rgba(255, 255, 255, 0.8);
  box-shadow: -1rem 0 1rem rgba(0, 0, 0, 0.2);
}
@media only screen and (max-width: 37.5em) {
  .sidebar {
    width: 100%;
  }
}

.sidebar li,
.sidebar li a {
  width: 100%;
  padding-bottom: 1.5rem;
}

.sidebar-menu-list {
  padding: 2rem 6rem;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}

.sidebar-menu-list li a {
  color: #333;
}

.icon-x {
  transition: 0.2s;
  padding: 2.5rem;
  font-size: 4rem;
}

.footer-box {
  display: flex;
  flex-direction: column;
  justify-content: center;
  flex-wrap: wrap;
  align-items: center;
}

.section {
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  border-radius: 40px;
  position: relative;
  margin: 0 30px;
}
.section--frontpage {
  background-image: url(../src/images/blue.jpg);
  height: 85vh;
}
.section--page {
  background-image: url(../src/images/blue.jpg);
  height: 35vh;
  display: block;
}

.text-box {
  text-align: center;
  position: absolute;
  transform: translate(-50%, -50%);
  top: 50%;
  left: 50%;
  width: 100%;
}

