.contact__icon {
  font-size: 2.5em;
  color: #333;
}
.contact__info-box {
  display: flex;
  justify-content: center;
}
@media only screen and (max-width: 37.5em) {
  .contact__info-box {
    flex-direction: column;
    align-items: center;
  }
}
.contact__info {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 17.5em;
  padding: 0 0.625em;
}
.contact__info:not(:last-child) {
  border-right: 2px solid #0077fe;
  margin-bottom: 2.5em;
}
@media only screen and (max-width: 37.5em) {
  .contact__info:not(:last-child) {
